.about-title {
  border-bottom: 1px solid white;
  font-size: 24px;
  margin-bottom: 20px;
  padding-bottom: 5px;
}

.about-article {
  font-size: 16px;
}

.about-founder {
  height: 150px;
  width: 150px;
  overflow: hidden;
  position: relative;

  img {
    position: absolute;
    top: -20px;
    width: 150px;
  }

  span {
    background: black;
    bottom: 0;
    color: white;
    font-size: 16px;
    left: 0;
    position: absolute;
    text-align: center;
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .about-title {
    font-size: 36px;
  }

  .about-article {
    font-size: 24px;
  }
  .about-founder {
    height: 300px;
    width: 300px;

    img {
      width: 300px;
    }

    span {
      font-size: 24px;
    }
  }
}
