body {
  color: white;
  background-color: black;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  min-height: 100%;
  min-width: 100%;

  @media (min-width: 577px) {
    background-position: center center;
  }
}

.modal-body {
  color: black;
}

h3 {
  margin: 40px 0;
}
